@import "./../../sassStyles/index";

.title5:hover {

    // animation: explode5 1s forwards ease-out;
    animation: explode5 1s ease-out;

    // animation-delay: 0.3s;

    @keyframes explode5 {
        0% {
            transform: translateY(0px);

        }

        100% {
            transform: translateY(-6px);
        }
    }
}

.header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    border: none;

    z-index: $z-index-overlay - 3;

    top: 0;
    right: 0;
    position: fixed;

    transition: all 0.4s ease-in-out;

    &--dark .header__wrapperButton span {
        background-color: rgba(243, 243, 243);
    }

    &--about .header__menu__wrapper {
        & a:nth-child(1) {
            display: none;
        }
    }

    &--services .header__menu__wrapper {
        & a:nth-child(2) {
            display: none;
        }
    }

    &--career .header__menu__wrapper {
        & a:nth-child(3) {
            display: none;
        }
    }

    &--contact .header__menu__wrapper {
        & a:nth-child(4) {
            display: none;
        }
    }

    &__wrapperButton {

        position: relative;
        width: 60px;
        height: 60px;
        margin-top: 30px;
        margin-right: 30px;

        z-index: $z-index-overlay - 1;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        transition: all 0.4s ease-in-out;

        & span {
            position: absolute;
            top: calc(50% - 1px);
            left: calc(50% - 20px);
            width: 35px;
            height: 5px;
            border-radius: 5px;
            background-color: $font-color;
            transform-origin: center center;
            transition: all 0.4s ease-in-out;
            cursor: pointer;
        }

        & span:nth-child(1) {
            transform: translateY(6px);
        }

        & span:nth-child(2) {
            transform: translateY(-6px);
        }

        &--active {
            & span:nth-child(1) {
                transform: rotate(45deg);
                background-color: $font-color;
            }

            & span:nth-child(2) {
                transform: rotate(-45deg);
                background-color: $font-color;
            }
        }
    }

    &__menu {
        display: flex;
        // justify-content: center;
        align-items: center;
        flex-direction: column;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: $z-index-overlay - 2;
        // background-color: rgb(25, 25, 25);
        // background-color: rgb(0, 27, 63);
        background-color: #FFF;

        visibility: hidden;
        opacity: 0;
        transition: all 0.1s ease-in-out;

        &--active {
            visibility: visible;
            opacity: 1;
        }

        &__wrapper {
            display: flex;
            flex-direction: column;
            justify-content: center;
            // color: rgba(243, 243, 243);
            color: $font-color;
            transition: all 1s ease-in-out;
            align-items: center;
            margin-top: 30vh;

            width: 20%;

            @media only screen and (max-width: $screen-sm) {
                width: 50%;
            }

            &:hover svg path {
                stroke: rgb(231, 231, 231);
                fill: rgb(231, 231, 231);
            }

            & a {
                width: 100%;
                padding-bottom: 4%;

                &:hover svg path {
                    stroke: $font-color;
                    fill: $font-color;
                }

                & svg {
                    width: 100%;
                    height: 100%;
                }

                & svg path {
                    fill: rgb(25, 25, 25);
                    stroke: rgb(25, 25, 25);
                    stroke-width: 6;
                    transition: all 0.8s ease-in-out;
                }
            }
        }

        &__icon {
            position: fixed;
            bottom: 2vh;
            left: 2vw;

            width: clamp(1vw, 25vw, 30vw);

            @media only screen and (min-width: $screen-lg) {
                width: clamp(1vw, 15vw, 30vw);
            }
        }
    }
}